import * as Sentry from '@sentry/react';
import distance from '@turf/distance';
import { memoize, memoizeResolvedPromise } from 'tools/memoize';

import searchRegionData from '../../generated/geometry/search-region-data.wkt.json';

import { getSuggests, DGSuggest } from './api/suggest';
import { DGLocale, isErrorResponse, DGLatLng } from './api/common';

const regionData = searchRegionData.data as Record<string, string>;
const searchRegionList = searchRegionData.regions;
const searchRegionGroupList = searchRegionData.groups;

const memoizedGetSuggests = memoizeResolvedPromise(getSuggests);

const isPointInBBox = (point: [number, number], bbox: number[]): boolean => {
    // prettier-ignore
    return (
        point[0] >= bbox[0] &&
        point[1] >= bbox[1] &&
        point[0] <= bbox[2] &&
        point[1] <= bbox[3]
    );
};

const getPrimaryPolygonList = memoize((lon: number, lat: number) => {
    const point: [number, number] = [lon, lat];

    return [...searchRegionGroupList]
        .filter(({ bbox }) => isPointInBBox(point, bbox))
        .map((item) => item.groupRegionList)
        .flat();
});

const getSecondaryPolygonList = memoize((lon: number, lat: number) => {
    const primaryPolygonList = getPrimaryPolygonList(lon, lat);

    return [...searchRegionList]
        .filter(({ name }) => primaryPolygonList.includes(name) === false)
        .map((item) => item.name);
});

const getDistance = (point: DGLatLng, userLocation: number[]) => {
    return distance([point.lat, point.lon], userLocation);
};

export interface GetSuggestsListParams {
    q: string;
    locale: DGLocale;
    longitude: string;
    latitude: string;
}

const defaultSuggestOptions = {
    fields: ['items.address', 'items.point'],
    suggestType: 'address',
    type: ['building', 'street'],
};

const getSuggestListByPolygonList = async (
    { q, locale, longitude, latitude }: GetSuggestsListParams,
    polygonList: string[],
) => {
    const suggestList = await Promise.all(
        polygonList.map((polygonName) =>
            memoizedGetSuggests({
                ...defaultSuggestOptions,
                q: q.toLowerCase(),
                locale,
                location: [latitude, longitude],
                polygon: regionData[polygonName] || '',
            })
                .then((response) => {
                    if (isErrorResponse(response)) {
                        throw new Error(`Error: 2GIS Suggests List: ${response.meta.error.message}`);
                    }

                    return response.result.items;
                })
                .catch((error) => {
                    if (process.env.REACT_APP_ENVIRONMENT === 'production') {
                        Sentry.captureException(error);
                    }

                    return [];
                }),
        ),
    ).then((responseList) => responseList.flat());

    const uniqueSuggestList = [
        ...suggestList
            .reduce((suggestMap, suggest) => {
                suggestMap.set(suggest.id, suggest);

                return suggestMap;
            }, new Map<string, DGSuggest>())
            .values(),
    ];

    const userLocation = [parseFloat(latitude), parseFloat(longitude)];

    const distanceMap = Object.fromEntries(
        uniqueSuggestList.map((suggest) => [
            suggest.id,
            suggest.point ? getDistance(suggest.point, userLocation) : Number.POSITIVE_INFINITY,
        ]),
    );

    const sortedSuggestsList = [...uniqueSuggestList].sort((a, b) => distanceMap[a.id] - distanceMap[b.id]);

    return sortedSuggestsList;
};

export const getSuggestsList = async (params: GetSuggestsListParams): Promise<DGSuggest[]> => {
    const { q, longitude, latitude } = params;

    if (q.length === 0) {
        return [];
    }

    const primaryPolygonList = getPrimaryPolygonList(parseFloat(longitude), parseFloat(latitude));
    const secondaryPolygonList = getSecondaryPolygonList(parseFloat(longitude), parseFloat(latitude));

    const primaryResult = await getSuggestListByPolygonList(params, primaryPolygonList);

    if (primaryResult.length) {
        return primaryResult;
    }

    const secondaryResult = await getSuggestListByPolygonList(params, secondaryPolygonList);

    return secondaryResult;
};
