import { StoreUser } from 'store/constants/user';
import { DGSuggest, isBuildingSuggest, isStreetSuggest } from 'tools/twoGis/api/suggest';
import { AddressSimpleSuggestItemView } from 'pages/AddressSimple/SuggestItem/AddressSimpleSuggestItem';

import { AppReducer, AppState, initialState } from '../../constants';

const prepareSuggestItems = (state: AppState) => {
    const {
        twoGisSuggestsData: { itemsMap },
        addressSimplePage: { inputValue },
    } = state;

    return Object.values(itemsMap)
        .filter((item) => {
            return isBuildingSuggest(item);
        })
        .map((item) => item.id);
};

