import { Spinner } from 'components/Spinner/Spinner';
import React from 'react';
import { addressSetSuggestsAction } from 'store/actions/addressSimplePage';
import { setTwoGisSuggests } from 'store/actions/twoGisSuggests';
import { addressInputDeclineThunk } from 'store/thunks/native';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressSubmitAction } from 'store/actions/server';
import { ROUTES } from 'store/constants/router';
import { HeadlineText1 } from 'components/Text/Headline';
import { AddressSimpleSuggestListItem } from 'pages/AddressSimple/SuggestItem/AddressSimpleSuggestItem';
import { Button, ButtonViews } from 'components/Button/Button';
import { color } from 'style/const/color';
import { DGSuggest, isBuildingSuggest } from 'tools/twoGis/api/suggest';
import { getSuggestsList } from 'tools/twoGis/getSuggestsList';

const StyledAnswerContainer = styled.div`
    display: flex;
    flex-direction: column;
`;

const StyledAnswerTitle = styled(HeadlineText1)`
    margin-top: 34px;
    color: ${color.text.primary};
    text-align: center;
`;

const StyledSource = styled(HeadlineText1)`
    position: relative;
    margin-top: 32px;

    background: linear-gradient(to right, #00ffdf, #00ff5a);
    color: transparent;
    -webkit-background-clip: text;

    text-align: center;
`;

const StyledContainer = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
`;

const StyledAnswerButtonContainer = styled.div`
    display: flex;
    margin-top: 34px;
    justify-content: center;
`;

const StyledButton = styled(Button)`
    flex-grow: 1;
    max-width: 200px;
`;

const StyledButtonDivider = styled.div`
    width: 34px;
`;

export const AddressSimpleQuestion: React.FC = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressSimplePage }) => addressSimplePage.inputValue);
    const latitude = useSelector(({ userData }) => userData.deviceLocationLatitude);
    const longitude = useSelector(({ userData }) => userData.deviceLocationLatitude);
    const suggest = useSelector(({ twoGisSuggestsData: { itemsMap } }) => {
        return Object.values(itemsMap).find((item) => isBuildingSuggest(item));
    });
    const handleAcceptButtonClick = React.useCallback(() => {
        dispatch(addressSubmitAction());
    }, [dispatch]);
    const handleDeclineButtonClick = React.useCallback(() => {
        dispatch(addressInputDeclineThunk());
    }, [dispatch]);

    React.useEffect(() => {
        if (!suggest) {
            getSuggestsList({ q: inputValue, locale: 'ru_RU', latitude, longitude })
                .then((items) => {
                    dispatch(setTwoGisSuggests(items));
                })
                .catch(() => {
                    dispatch(setTwoGisSuggests([]));
                });
        }
    }, [dispatch, inputValue, latitude, longitude, suggest]);

    return (
        <StyledContainer>
            {suggest ? (
                <StyledAnswerContainer>
                    <StyledSource>{suggest.full_name}</StyledSource>
                    <StyledAnswerTitle>  ?</StyledAnswerTitle>
                    <StyledAnswerButtonContainer>
                        <StyledButton shouldFocusOnMount view={ButtonViews.secondary} onClick={handleAcceptButtonClick}>
                            
                        </StyledButton>
                        <StyledButtonDivider />
                        <StyledButton view={ButtonViews.secondary} onClick={handleDeclineButtonClick}>
                            
                        </StyledButton>
                    </StyledAnswerButtonContainer>
                </StyledAnswerContainer>
            ) : (
                <Spinner />
            )}
        </StyledContainer>
    );
};
