import { useFocusOnMount } from 'hooks/useFocusOnMount';
import { useThrottledFn } from 'hooks/useThrottledFn';
import React from 'react';
import { removeItemAction } from 'store/actions/native';
import { addressSubmitAction } from 'store/actions/server';
import { focusBorderMixin } from 'style/mixin/focus';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { addressClickSuggestItemAction, addressSelectSuggestItemAction } from 'store/actions/addressSimplePage';
import { font } from 'style/const/font';
import { transition } from 'style/const/transition';
import { color } from 'style/const/color';

const StyledRoot = styled.div`
    height: 112px;
    padding: 36px 32px;
    border: 4px solid transparent;
    box-sizing: border-box;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    border-radius: 24px;
    font-weight: ${font.weight.medium};
    font-size: 32px;
    color: #fff;
    background: ${color.surface.liquid1};

    transition: border ${transition.default};

    &:focus {
        border-color: #68ea68;
    }
`;

interface SuggestListItemProps {
    id: string;
    shouldFocusOnMount?: boolean;
}

export interface AddressSimpleSuggestItemView {
    id: string;
    name: string;
}
export const AddressSimpleSuggestListItem: React.FC<SuggestListItemProps> = ({ id, shouldFocusOnMount }) => {
    const { full_name: name } = useSelector(({ twoGisSuggestsData: { itemsMap } }) => itemsMap[id]);
    const someRef = React.useRef<HTMLDivElement>(null);
    const onMountRef = useFocusOnMount<HTMLDivElement>();
    const ref = shouldFocusOnMount ? onMountRef : someRef;
    const dispatch = useDispatch();
    const memoizedOnClick = useThrottledFn(
        () => {
            dispatch(addressSubmitAction());
        },
        200,
        undefined,
        [dispatch],
    );
    const memoizedOnFocus = React.useCallback(() => {
        dispatch(addressSelectSuggestItemAction(id));
    }, [dispatch, id]);

    return (
        <StyledRoot ref={ref} tabIndex={0} onClick={memoizedOnClick} onFocus={memoizedOnFocus}>
            {name}
        </StyledRoot>
    );
};
