import { BodyText1 } from 'components/Text/Body';
import { HeadlineText1 } from 'components/Text/Headline';
import React from 'react';
import { color } from 'style/const/color';
import styled from 'styled-components';
import Swiper from 'swiper';
import { useFocusOnChange } from 'hooks/useFocusOnChange';
import { useKeyboardExpanded } from 'hooks/useKeyboardExpanded';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { isBuildingSuggest } from 'tools/twoGis/api/suggest';
import { getSuggestsList } from 'tools/twoGis/getSuggestsList';
import { addressChangeInputValueAction, addressSetSuggestsAction } from 'store/actions/addressSimplePage';
import { addressSubmitAction } from 'store/actions/server';
import { Input } from 'components/Input/Input';
import { Button, ButtonViews } from 'components/Button/Button';
import { ReactComponent as KeyboardSvg } from 'pages/AddressSimple/AddressSimple.assets/keyboard.svg';

import { AddressSimpleSuggestListItem } from './SuggestItem/AddressSimpleSuggestItem';

const StyledTitle = styled(BodyText1)``;
const StyledKeyboardButton = styled.div``;

const StyledKeyboardIcon = styled(KeyboardSvg)`
    height: 64px;
    width: 64px;
`;

const StyledKeyboardExpandButton = styled.div`
    height: 112px;
    width: 112px;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    background: rgba(255, 255, 255, 0.06);
    border-radius: 24px;
`;

const StyledContainer = styled.div`
    display: flex;
    align-items: start;
    height: 100%;
    padding: 160px 360px 0;
`;

const StyledSwiperContainer = styled.div`
    margin-left: 24px;
    height: ${(112 + 24) * 4 - 24}px;
    overflow: hidden;
    transition: height 0.5s ease-in-out;
`;

const StyledSwiperWrapper = styled.div``;

const preventScroll = (event: React.SyntheticEvent<HTMLDivElement>) => {
    event.currentTarget.scrollTop = 0;
};

const StyledSwiperSlide = styled.div`
    height: 112px;
`;

export const AddressSimple: React.FC = () => {
    // const suggests = useSelector(({ twoGisSuggestsData: { itemsMap } }) => {
    //     return Object.values(itemsMap).filter((item) => {
    //         return isBuildingSuggest(item);
    //     });
    // });

    const suggestsListIndex = useSelector(({ addressSimplePage }) => addressSimplePage.suggestsListIndex);
    const suggestIds = useSelector(({ addressSimplePage }) => addressSimplePage.suggests);
    const swiperContainerRef = React.useRef<HTMLDivElement>(null);
    const swiperRef = React.useRef<Swiper | null>(null);

    React.useEffect(() => {
        if (swiperContainerRef.current) {
            swiperRef.current =
                swiperRef.current ||
                new Swiper(swiperContainerRef.current, {
                    direction: 'vertical',
                    slidesPerView: 'auto',
                    speed: 400,
                    spaceBetween: 24,
                    initialSlide: 0,
                    // roundLengths: true,
                });
        }
    }, []);

    React.useLayoutEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.updateSize();
            swiperRef.current.updateSlides();
            swiperRef.current.updateProgress();
            swiperRef.current.updateSlidesClasses();
        }
    }, [suggestIds]);

    React.useLayoutEffect(() => {
        if (swiperRef.current) {
            console.log('slide tol', suggestsListIndex - 1);
            swiperRef.current.slideTo(suggestsListIndex - 1, 400);
        }
    }, [suggestsListIndex]);

    const suggestItems = React.useMemo(() => {
        return suggestIds.map((itemId, index) => (
            <StyledSwiperSlide className="swiper-slide" key={itemId}>
                <AddressSimpleSuggestListItem shouldFocusOnMount={index === 0} id={itemId} />
            </StyledSwiperSlide>
        ));
    }, [suggestIds]);

    const handleExpandKeyboardButton = React.useCallback(() => {
        console.log('some');
    }, []);

    // <StyledKeyboardExpandButton tabIndex={0} onClick={handleExpandKeyboardButton}>
    // </StyledKeyboardExpandButton>

    return (
        <StyledContainer>
            <Button view={ButtonViews.secondary} onClick={handleExpandKeyboardButton}>
                <StyledKeyboardIcon />
            </Button>
            <StyledSwiperContainer className="swiper-container" ref={swiperContainerRef} onScroll={preventScroll}>
                <StyledSwiperWrapper className="swiper-wrapper">{suggestItems}</StyledSwiperWrapper>
            </StyledSwiperContainer>
        </StyledContainer>
    );
};
