export type AppState = typeof initialState;

export type AppCommonAction = ReturnType<AppActionTypes[keyof AppActionTypes]>;
export type AppBaseAction = AppCommonAction | RouterAction<LocationState>;
export type AppThunkAction = ThunkAction<void, AppState, {}, AppBaseAction>;
export type AppAction = AppThunkAction | AppBaseAction;
export type AppDispatch = (action: AppAction) => AppBaseAction;
export type AppReducer = (state: AppState, action: AppBaseAction) => void;
export interface AppMiddleware {
    (api: AppMiddlewareAPI): (next: AppDispatch) => (action: AppBaseAction) => AppBaseAction;
}

export interface AppMiddlewareAPI {
    dispatch: AppDispatch;
    getState(): AppState;
}

export interface CartItem {
    itemId: string;
    count: number;
}
